export default "@import 'taiga-ui-local';\n\n.container {\n    max-width: 46.875rem;\n}\n\n.operation-header {\n    font: var(--tui-font-text-l);\n    display: flex;\n    align-items: center;\n}\n\n.operation-date {\n    font: var(--tui-font-text-xl);\n    text-align: center;\n}\n\n.operation-date,\n.operation-info {\n    color: var(--tui-text-02);\n}\n\n.operation-month,\n.operation-status {\n    font: var(--tui-font-text-s);\n}\n\n.operation-pic {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    flex-shrink: 0;\n    margin: 0 1.5rem;\n    background: var(--tui-secondary-active);\n    border-radius: 100%;\n    width: 3rem;\n    height: 3rem;\n    color: var(--tui-text-03);\n}\n\n.operation-title {\n    .text-overflow();\n    margin-right: 1.5rem;\n    flex-grow: 1;\n}\n\n.operation-info {\n    text-align: right;\n}\n\n.operation-status {\n    &_success {\n        color: var(--tui-success-fill);\n    }\n\n    &_error {\n        color: var(--tui-error-fill);\n    }\n}\n\n.form-title {\n    font: var(--tui-font-heading-6);\n    margin: 0 0 1.25rem;\n}\n\n.input {\n    max-width: 28.75rem;\n    margin-bottom: 1.25rem;\n}\n\n.buttons {\n    display: flex;\n}\n";